/*
	HTML5 Reset :: style.css
	----------------------------------------------------------
	We have learned much from/been inspired by/taken code where offered from:

	Eric Meyer					:: http://meyerweb.com
	HTML5 Doctor				:: http://html5doctor.com
	and the HTML5 Boilerplate	:: http://html5boilerplate.com

-------------------------------------------------------------------------------*/
/* Let's default this puppy out
-------------------------------------------------------------------------------*/
@import url("//fonts.googleapis.com/css?family=Noto+Sans");
@import url("//fonts.googleapis.com/earlyaccess/notosansarmenian.css");
@import url("//fonts.googleapis.com/earlyaccess/notosansbengali.css");
@import url("//fonts.googleapis.com/earlyaccess/notosansgujarati.css");
@import url("//fonts.googleapis.com/earlyaccess/notosanskannada.css");
@import url("//fonts.googleapis.com/earlyaccess/notosanssinhala.css");
@import url("//fonts.googleapis.com/earlyaccess/notosanstamil.css");
@import url("//fonts.googleapis.com/earlyaccess/notosanstelugu.css");
@import url("//fonts.googleapis.com/earlyaccess/notosansethiopic.css");
@import url("//fonts.googleapis.com/earlyaccess/notosansmalayalam.css");
@import url("//fonts.googleapis.com/earlyaccess/notosansgurmukhi.css");
html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  background: transparent;
}
article, aside, figure, footer, header, nav, section, details, summary {
  display: block;
}

/* Handle box-sizing while better addressing child elements:
   http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

/* consider resetting the default cursor: https://gist.github.com/murtaugh/5247154 */
/* Responsive images and other embedded objects */
/* if you don't have full control over `img` tags (if you have to overcome attributes), consider adding height: auto */
img,
object,
embed {
  max-width: 100%;
}

/*
   Note: keeping IMG here will cause problems if you're using foreground images as sprites.
	In fact, it *will* cause problems with Google Maps' controls at small size.
	If this is the case for you, try uncommenting the following:

#map img {
		max-width: none;
}
*/
/* force a vertical scrollbar to prevent a jumpy page */
html {
  overflow-y: scroll;
}

/* we use a lot of ULs that aren't bulleted.
	you'll have to restore the bullets within content,
	which is fine because they're probably customized anyway */
ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
del {
  text-decoration: line-through;
}
abbr[title], dfn[title] {
  border-bottom: 1px dotted #000;
  cursor: help;
}

/* tables still need cellspacing="0" in the markup */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
th {
  font-weight: bold;
  vertical-align: bottom;
}
td {
  font-weight: normal;
  vertical-align: top;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
input, select {
  vertical-align: middle;
}
pre {
  white-space: pre;
  /* CSS2 */
  white-space: pre-wrap;
  /* CSS 2.1 */
  white-space: pre-line;
  /* CSS 3 (and 2.1 as well, actually) */
  word-wrap: break-word;
  /* IE */
}
input[type="radio"] {
  vertical-align: text-bottom;
}
input[type="checkbox"] {
  vertical-align: bottom;
}
.ie7 input[type="checkbox"] {
  vertical-align: baseline;
}
.ie6 input {
  vertical-align: text-bottom;
}
select, input, textarea {
  font: 99% sans-serif;
}
table {
  font-size: inherit;
  font: 100%;
}
small {
  font-size: 85%;
}
strong {
  font-weight: bold;
}
td, td img {
  vertical-align: top;
}

/* Make sure sup and sub don't mess with your line-heights http://gist.github.com/413930 */
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}

/* standardize any monospaced elements */
pre, code, kbd, samp {
  font-family: monospace, sans-serif;
}

/* hand cursor on clickable elements */
.clickable,
label,
input[type=button],
input[type=submit],
input[type=file],
button {
  cursor: pointer;
}

/* Webkit browsers add a 2px margin outside the chrome of form elements */
button, input, select, textarea {
  margin: 0;
}

/* make buttons play nice in IE */
button,
input[type=button] {
  width: auto;
  overflow: visible;
}

/* scale images in IE7 more attractively */
.ie7 img {
  -ms-interpolation-mode: bicubic;
}

/* prevent BG image flicker upon hover
   (commented out as usage is rare, and the filter syntax messes with some pre-processors)
.ie6 html {filter: expression(document.execCommand("BackgroundImageCache", false, true));}
*/
/* let's clear some floats */
.clearfix:before, .clearfix:after {
  content: "\0020";
  display: block;
  height: 0;
  overflow: hidden;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  zoom: 1;
}
input.translation.hy {
  font-family: 'Noto Sans Armenian', sans-serif;
}
input.translation.bn {
  font-family: 'Noto Sans Bengali', sans-serif;
}
input.translation.gu {
  font-family: 'Noto Sans Gujarati', sans-serif;
}
input.translation.kn {
  font-family: 'Noto Sans Kannada', sans-serif;
}
input.translation.si {
  font-family: 'Noto Sans Sinhala', sans-serif;
}
input.translation.ta {
  font-family: 'Noto Sans Tamil', sans-serif;
}
input.translation.te {
  font-family: 'Noto Sans Telugu', sans-serif;
}
input.translation.am {
  font-family: 'Noto Sans Ethiopic', sans-serif;
}
input.translation.ml {
  font-family: 'Noto Sans Malayalam', sans-serif;
}
input.translation.pa {
  font-family: 'Noto Sans Gurmukhi', sans-serif;
}
html {
  overflow: hidden;
}
body, input, textarea {
  font-family: 'Noto Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  background: #eaeaec;
  color: black;
}
#app {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.small {
  font-size: 12px;
}
.controls {
  background: #555;
}
h1 {
  position: absolute;
  top: 0;
  left: 32px;
  font-size: 14px;
  line-height: 60px;
  padding-left: 30px;
  background: url("http://www.ericpitcock.com/static/img/e.svg") left center no-repeat;
}
.select-control {
  position: absolute;
  top: 67px;
  left: 20px;
  text-align: right;
  width: 200px;
  z-index: 1;
}
.select-control button {
    border: none;
    background: none;
    font-size: 10px;
    padding: 0;
    margin-left: 5px;
    color: red;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.select-control button:hover {
      color: black;
}
.select-control button:focus {
      outline: none;
}
.languages {
  position: absolute;
  top: 60px;
  bottom: 30px;
  left: 30px;
  width: 200px;
  overflow: scroll;
  padding: 26px 30px 30px 30px;
  background: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
}
.languages::-webkit-scrollbar {
    display: none;
}
.languages label {
    display: block;
    margin-bottom: 10px;
}
.languages label input {
      margin-right: 5px;
      vertical-align: baseline;
}
.languages label input:hover {
        cursor: pointer;
}
.languages label input:focus {
        outline: none;
}
.languages .th {
    padding-bottom: 20px;
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 20px;
}
.input-container {
  position: absolute;
  top: 60px;
  left: 260px;
  width: 100%;
  max-width: 450px;
  height: 60px;
}
.input-container input.word-input {
    width: 100%;
    height: 100%;
    border: none;
    padding-left: 30px;
    line-height: 60px;
    background: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
    transition: all .2s ease-in-out;
    -webkit-appearance: none;
}
.input-container input.word-input::-webkit-scrollbar {
      display: none;
}
.input-container input.word-input::-webkit-input-placeholder {
      color: black;
}
.input-container input.word-input:-ms-input-placeholder {
      color: black;
}
.input-container input.word-input::placeholder {
      color: black;
}
.input-container input.word-input:focus {
      outline: none;
      border-color: #999;
      box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1);
}
.input-container .clear-input {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    cursor: pointer;
    font-size: 24px;
    line-height: 56px;
    text-align: center;
    color: #ccc;
}
.input-container .clear-input:hover {
      color: red;
}
.translation-cont {
  position: absolute;
  top: 150px;
  right: 30px;
  bottom: 30px;
  left: 260px;
  max-width: 700px;
  overflow: scroll;
  background: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
}
.translation-cont::-webkit-scrollbar {
    display: none;
}
.translation-cont .loading {
    height: 2px;
    position: absolute;
    background: red;
}
.translation-cont table.output {
    width: 100%;
    margin: 30px 0;
    text-align: left;
}
.translation-cont table.output thead {
      text-transform: uppercase;
      letter-spacing: 1px;
      color: blue;
}
.translation-cont table.output thead tr {
        border-bottom: 1px solid #e6e6e6;
}
.translation-cont table.output thead th {
        width: 33.333%;
        padding-bottom: 10px;
}
.translation-cont table.output tbody tr {
      border-bottom: 1px solid #e6e6e6;
}
.translation-cont table.output tbody tr:nth-child(odd) {
        background: #f9f9f9;
}
.translation-cont table.output tbody tr td {
        height: 40px;
        vertical-align: middle;
}
.translation-cont table.output tbody tr td span.lang-label {
          display: inline-block;
          font-size: 10px;
          text-transform: uppercase;
          letter-spacing: 1px;
          color: #999;
          padding-left: 5px;
}
.translation-cont table.output tbody tr td input.translation {
          border: none;
          background: none;
          cursor: pointer;
}
.translation-cont table.output tbody tr td input.translation:hover {
            color: red;
}
.translation-cont table.output th:first-child,
    .translation-cont table.output td:first-child {
      padding-left: 30px;
}
.fade-enter-active {
  transition: opacity 1s ease-in-out;
}
.fade-leave-active {
  transition: opacity .3s ease-in-out;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



